import React, { useEffect, useState, useCallback } from 'react'
import { Typography, Grid } from '@mui/material'
import { Formik, Form } from 'formik'
import Swal from 'sweetalert2'
import TextInput from 'components/inputs/TextInput'
import MaskedTextField from 'components/inputs/MaskedTextField'
import CheckField from 'components/inputs/CheckField'
import SimpleSelect from 'components/inputs/SimpleSelect'
import SelectInput from 'components/inputs/SelectInput'
import QueueInformer from './QueueInformer'
import Section from './layout/Section'
import { CircularProgress, Button } from '@mui/material'
import LinearProgressWithLabel from './LinearProgressWithLabel'
import { getFormData } from 'helpers/normalizeData'
import { dayNames } from 'helpers/date'
import getDay from 'date-fns/getDay'
import parse from 'date-fns/parse'
import addDays from 'date-fns/addDays'
import format from 'date-fns/format'
import { hubAction } from 'api'
import * as yup from 'yup'
import { parseDateString } from 'helpers/date'
import { cities } from 'helpers/toponyms'

//TEST
// import StormTest from './StormTest'

// async function stall(stallTime = 3000) {
//   await new Promise(resolve => setTimeout(resolve, stallTime));
// }

const initialValues = {
  last_name: '',
  first_name: '',
  middle_name: '',
  inn: '',
  tel: '',
  vpoNumber: '',
  vpoDate: '',
  city: '',
  agree: false,
  checked: false
}

const phoneNumberRegex =
  /\(?([0-9]{3})\)?([0-9]{3})[-. ]?([0-9]{2})[-. ]?([0-9]{2})$/

const today = new Date()
const minDate = new Date('2022-02-24')
const oneDayBeforeMinDate = addDays(minDate, -1)

const validationSchema = yup.object({
  last_name: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(2, '  '),
  first_name: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(2, "  '"),
  middle_name: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(4, '     4 '),
  tel: yup
    .string()
    .required("  '")
    .matches(
      phoneNumberRegex,
      '  , (0)--'
    ),
  inn: yup
    .string()
    .required("  '")
    .length(10, '    10 '),
  vpoNumber: yup
    .string()
    .required("  '")
    .matches(/^\d{4}[-]\d{10}$/, '  (1234-1234567890)'),
  vpoDate: yup
    .date()
    .required("  '")
    .transform(parseDateString)
    .typeError(' ,      ..')
    .min(oneDayBeforeMinDate, `  ${format(minDate, 'dd.MM.yyyy')}`)
    .max(today, '  ? '),
  city: yup
    .string()
    .required("  '"),
  agree: yup
    .boolean()
    .oneOf([true], ' ,   '),
})

const services = [
  {
    id: 1,
    name: '    "  "',
    title: `  
              "  "`,
    description: ` :            .

        '     .
             , :
          ,     ;
             ( )  ,  ;
           ,     (   ).

       : .  , 9 (   ""      ).    !`,
    action: hubAction
  }
]
const QueueForm = () => {
  const [service, setService] = useState(null)
  const [isLoading, setLoading] = useState(true)
  const [isClosed, setClosed] = useState(true)
  const [availableCount, setAvailableCount] = useState(100)

  const isFormVisible = service && !isLoading && !isClosed

  const getAvailableCount = (max, current) => {
    max = +max
    current = +current
    if (isNaN(max) || isNaN(current)) {
      return 0
    }
    return Math.ceil(100 - (current / max * 100))
  }

  const isFormClosed = useCallback(
    async (action) => {
      try {
        await fetch(action, { method: 'GET' })
          .then(res => res.json())
          .then(data => {
            // console.log(data)
            if (data?.status === 'open') {
              // console.log(data)
              setClosed(false)
              setAvailableCount(getAvailableCount(data?.max, data?.current))
            }
            return null
          })
      } catch (e) {
        throw new Error(e)
      } finally {
        setLoading(false)
      }
    }, []
  )

  useEffect(() => {
    if (service) {
      setClosed(isFormClosed(service?.action))
    }
  }, [service, isFormClosed])

  const handleSubmit = async (values, helpers) => {
    const toNormalize = ['first_name', 'last_name', 'middle_name']
    const toExclude = ['agree', 'checked']

    const data = getFormData(
      values,
      toNormalize,
      toExclude
    )

    try {
      if (!service?.action) return null
      await fetch(hubAction, {
        method: 'POST',
        body: data

      })
        .then((response) => response.json())
        .then((data) => {
          // console.log(data)

          switch (data?.result) {
            case 'closed':
            case 'refused':
              Swal.fire({
                title: '',
                text: ` .       .`,
                icon: 'error',
                confirmButtonText: '',
                allowOutsideClick: false,
                allowEscapeKey: false,
              }).then((result) => {
                if (result.isConfirmed) {
                  helpers?.resetForm()
                  window.location.reload()
                }
              })
              break
            case 'success':
              const { number, date, time } = data
              const day = dayNames[getDay(parse(date, 'dd.MM.yyyy', new Date()))]
              // const dateString = format(parseISO(data?.date), 'dd.MM.yyyy')
              // const time = format(parseISO(data?.time), 'hh:mm')
              Swal.fire({
                title: '   ',
                html:
                  `<div style='text-align: left'>
                  <table style='margin: 0 auto; border: 2px solid #a5dc86; padding: 0.5em; border-spacing: 10px;'>
                    <tbody>
                      <tr><td> : </td>           <th> ${number}</strong></th></tr>
                      <tr><td>: </td>            <th> ${date} (${day.toLowerCase()})</th></tr>
                      <tr><td>*: </td>            <th> ${time} </th></tr>
                    </tbody>
                  </table>
    
                  <br><br>
                  <b>  :</b> . , . , 9 (   ""      ).    !<br><br>
                         . <br><br>
                  * -  ,   
                  </div>
                  `,
                icon: 'success',
                allowOutsideClick: false,
                showConfirmButton: false,
                allowEscapeKey: false,
                allowEnterKey: false,
                confirmButtonText: ''
              })
              helpers?.resetForm()
              break
            case 'timeout':
              Swal.fire('', '  .  .', 'warning')
              break
            case 'error':
              console.log('error', data.error)
              Swal.fire({
                title: '',
                text: ` .`,
                icon: 'error',
                confirmButtonText: ''
              })
              break
            default:
              console.log('Uknown answer from the server')
          }
        })
    } catch (error) {
      throw new Error(error)
    } finally {
      helpers?.setSubmitting(false)
    }
  }

  return (
    <Section sx={{ textAlign: 'center' }}>

      {!service && (
        <Section style={{ display: 'flex', flexDirection: 'column' }}>
          <Typography color="inherit" variant="h5" component="div" sx={{ mb: 4 }}>
             
          </Typography>

          <SimpleSelect
            name="services"
            label=" "
            options={services}
            defaultValue=""
            setOption={setService}
            current={service?.name}
            fullWidth
          />
        </Section>
      )}


      {/* <StormTest count={10} /> */}

      {service && isLoading && (
        <>
          <div> ...</div>
          <br />
          <div><CircularProgress size="3rem" /></div>
        </>
      )}

      {isClosed && !isLoading && (' .       .')}


      {isFormVisible && (
        <>

          <Typography color="inherit" variant="h6" component="div" sx={{ mb: 4 }}>
            {service?.title.split('\n').map((line, k) => (
              <div key={k}>{line} <br /></div>
            ))}
          </Typography>

          {/* <Typography color="inherit" variant="p" component="div" sx={{ mb: 4, textAlign: 'left' }}>
            {service?.description.split('\n').map((line, k) => (
              <div key={k}>{line} <br /></div>
            ))}
          </Typography> */}
          <LinearProgressWithLabel variant="determinate" value={availableCount} />
          <Formik
            initialValues={initialValues}
            // validateOnChange={false}
            // validateOnBlur={false}
            validationSchema={validationSchema}
            onSubmit={(values, actions) => {
              handleSubmit(values, actions)
            }}
          >
            {({ values, isSubmitting, handleSubmit }) => (

              <Form onSubmit={handleSubmit}>

                {isSubmitting ? (
                  <>
                    <div>  .    .</div> <br />
                    <CircularProgress size="3rem" />
                  </>
                ) : (
                  <>
                    <TextInput name="last_name" label="" fullWidth />
                    <TextInput name="first_name" label="'" fullWidth />
                    <TextInput name="middle_name" label="-" fullWidth />

                    <Grid container columnSpacing={2} columns={12}>
                      <Grid item xs={12} sm={6}>
                        <MaskedTextField
                          name="tel"
                          label=" "
                          type="tel"
                          format="+38(###)###-##-##"
                          // valueIsNumericString={true}
                          mask="_"
                          formatResult={true}
                          fullWidth
                          disabled={values?.checked}
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <MaskedTextField
                          name="inn"
                          label="  ()"
                          format="##########"
                          type="tel"
                          mask="_"
                          valueIsNumericString={true}
                          fullWidth
                          disabled={values?.checked}
                        />
                      </Grid>
                    </Grid>

                    <Grid container columnSpacing={2} columns={12}>
                      <Grid item xs={12} sm={7}>
                        <MaskedTextField
                          name="vpoNumber"
                          label="  "
                          format="####-##########"
                          // valueIsNumericString={true}
                          formatResult={true}
                          mask="_"
                          type="tel"
                          fullWidth
                        />
                      </Grid>
                      <Grid item xs={12} sm={5}>
                        <MaskedTextField
                          name="vpoDate"
                          label="  "
                          format="##.##.####"
                          mask="_"
                          type="tel"
                          // valueIsNumericString={true}
                          formatResult={true}
                          fullWidth
                        />
                      </Grid>
                    </Grid>

                    <SelectInput
                      name="city"
                      label=" "
                      options={cities}
                    />

                    <CheckField
                      name="agree"
                      label="     "
                      sx={{ mb: 2, textAlign: 'left' }}
                    />

                    <Button
                      startIcon={isSubmitting ? <CircularProgress size="1rem" /> : null}
                      disabled={isSubmitting}
                      variant="contained"
                      color="primary"
                      size="large"
                      type="submit"
                    >
                      {isSubmitting ? '' : ''}
                    </Button>
                  </>
                )}



              </Form>
            )}

          </Formik>

        </>
      )}
      <QueueInformer />
    </Section>
  )
}

export default QueueForm