
import FormikStep from 'components/FormikStep'
import TextInput from 'components/inputs/TextInput'
import MaskedTextField from 'components/inputs/MaskedTextField'
import { Grid } from '@mui/material'
import * as yup from 'yup'
import { parseDateString } from 'helpers/date'

const today = new Date()
const minDate = new Date('2022-02-23')

const vpoSchema = yup.object({
  factAddress: yup
    .string()
    .required("  '")
    .min(8, '   (, ,  ./.'),
  vpoNumber: yup
    .string()
    .required("  '")
    .matches(/^\d{4}[-]\d{10}$/, '  (1234-1234567890)'),
  vpoDate: yup
    .date()
    .required("  '")
    .transform(parseDateString)
    .typeError(' ,      ..')
    .min(minDate, '  24.02.2022')
    .max(today, '  ? '),
})

const StepVPO = () => {
  return (
    <FormikStep>
      <TextInput
        name="factAddress"
        label="     "
        helperText=", ., ., 1"
        fullWidth
      />

      <Grid container columnSpacing={2} columns={12}>
        <Grid item xs={12} sm={7}>
          <MaskedTextField
            name="vpoNumber"
            label="  "
            format="####-##########"
            // valueIsNumericString={true}
            formatResult={true}
            mask="_"
            type="tel"
            fullWidth
          />
        </Grid>
        <Grid item xs={12} sm={5}>
          <MaskedTextField
            name="vpoDate"
            label="  "
            format="##.##.####"
            mask="_"
            type="tel"
            // valueIsNumericString={true}
            formatResult={true}
            fullWidth
          />
        </Grid>
      </Grid>
    </FormikStep>
  )
}

export { StepVPO, vpoSchema }