import FormikStep from 'components/FormikStep'
import TextInput from 'components/inputs/TextInput'
import MaskedTextField from 'components/inputs/MaskedTextField'
import SelectInput from 'components/inputs/SelectInput'
import { Grid } from '@mui/material'

import * as yup from 'yup'
import { parseDateString } from 'helpers/date'
import { add } from 'date-fns'

const today = new Date()
const minDate = add(today, { years: -18 })

const passportRegex =
  /^(?:[-]{2}\d{6}|\d{9}|)$/

const personalSchema = yup.object({
  lastName: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(2, '  '),
  firstName: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(2, "  '"),
  middleName: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(4, '     4 '),
  birthday: yup
    .date()
    .required("  '")
    .transform(parseDateString)
    .typeError(' ,      ..')
    .max(minDate, '  18 '),
  passport: yup
    .string()
    .matches(
      passportRegex,
      ' , : 123456  001234567. '
    ).required("  '.         -    ,       \"\""),
  familySize: yup
    .number()
    .required("  '")
    .min(1, '  - 1'),
  children: yup
    .number()
    .required("  '.    -  0.")
    .min(0, '  - 0'),
})

const StepPersonal = () => {
  console.log(minDate)
  return (
    <FormikStep>
      <TextInput name="lastName" label="" fullWidth />
      <TextInput name="firstName" label="'" fullWidth />
      <TextInput name="middleName" label="-" fullWidth />
      <MaskedTextField
        name="birthday"
        label=" "
        format="##.##.####"
        mask="_"
        type="tel"
        // valueIsNumericString={true}
        formatResult={true}
        fullWidth
      />

      <TextInput name="passport" label=",  " fullWidth />

      <SelectInput
        name="socialStatus"
        label="    ( )"
        options={[
          '',
          '',
          ' ',
          '  ',
          '  ()',
          ' ',
          ' '
        ]}
        defaultValue=""
        fullWidth
      />

      <Grid container columnSpacing={2}>
        <Grid item xs={12} sm={6}>
          <TextInput
            type="number"
            name="familySize"
            label=" "
            fullWidth
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextInput
            type="number"
            name="children"
            label=" "
            fullWidth
          />
        </Grid>
      </Grid>
    </FormikStep>
  )
}

export {
  StepPersonal,
  personalSchema
}