import React, { useEffect, useState, useCallback, createContext } from 'react'
import { Typography } from '@mui/material'
import { Formik, Form } from 'formik'
import ToggleButtons from 'components/inputs/ToggleButtons'
import { CircularProgress, Button } from '@mui/material'
import LinearProgressWithLabel from '../../LinearProgressWithLabel'
import { clothesAction } from 'api'
import * as yup from 'yup'
import ClothesFormStepper from 'components/forms/ClothesForm/ClothesFormStepper'
import { StepParent, parentSchema } from 'components/forms/ClothesForm/StepParent'
import { StepChild, childSchema } from 'components/forms/ClothesForm/StepChild'


const initialValues = {
  check_delivery: false,
  last_name: '',
  first_name: '',
  middle_name: '',
  inn: '',
  tel: '',
  vpo_number: '',
  child_doc: '',
  child_bday: '',
  size: '',
  agree: false,
}


export const FormContext = createContext(null)

const ClothesForm = () => {

  const [isLoading, setLoading] = useState(false) //true
  const [isClosed, setClosed] = useState(false) // true
  const [availableCount, setAvailableCount] = useState(100)
  const [availableSizes, setAvailableSizes] = useState([])
  const [datestamp, setDatestamp] = useState(null)
  const [isConfirm, setConfirm] = useState(false)

  const getAvailableCount = (max, current) => {
    max = +max
    current = +current
    if (isNaN(max) || isNaN(current)) {
      return 0
    }
    return Math.ceil(current / max * 100)
  }

  const getAvailableSizes = (array) => {
    const avSizes = array.filter(a => +a[2] !== 0)
    const sizes = avSizes.map(item => {
      const available = Math.ceil(item[2] / item[3] * 100)
      return {
        id: item[0],
        label: item[1],
        available
      }
    })
    return sizes
  }

  const isFormClosed = useCallback(
    async (action) => {
      try {
        await fetch(action, { method: 'GET' })
          .then(res => res.json())
          .then(data => {
            if (data?.status === 'open') {
              setClosed(false)
              setDatestamp(data?.datestamp)
              setAvailableCount(getAvailableCount(data?.max, data?.current))
              setAvailableSizes(getAvailableSizes(data?.sizes))
            } else {
              setClosed(true)
            }
            return null
          })
      } catch (e) {
        throw new Error(e)
      } finally {
        setLoading(false)
      }
    }, []
  )

  useEffect(() => {
    if (isConfirm) {
      isFormClosed(clothesAction)
    }


  }, [isConfirm, isFormClosed])


  return (
    <FormContext.Provider value={{ availableSizes, isFormClosed }}>
      <div style={{ marginBottom: '2em' }}>


        <Typography color="inherit" variant="h6" component="div" sx={{ mb: 4 }}>
              
        </Typography>

        {!isConfirm && (
          <Formik
            initialValues={{
              check_delivery: false
            }}
            validationSchema={
              yup.object({
                check_delivery: yup
                  .boolean()
                  .oneOf([true], '     '),
              })
            }
            onSubmit={(values, actions) => {
              setLoading(true)
              setConfirm(true)
            }}
          >
            {({ isSubmitting, values }) => (

              <Form>
                <ToggleButtons name="check_delivery" label={'       ,   ?'} />
                <Button
                  startIcon={isSubmitting ? <CircularProgress size="1rem" /> : null}
                  disabled={isSubmitting}
                  variant="contained"
                  color="primary"
                  size="large"
                  type="submit"
                >
                  {isSubmitting ? '' : ''}
                </Button>
              </Form>

            )}
          </Formik>
        )}



        {isLoading && (
          <>
            <div>...</div>
            <br />
            <div><CircularProgress size="3rem" /></div>
          </>
        )}

        {isClosed && (' .')}


        {!isClosed && !isLoading && isConfirm && (

          <>
            <LinearProgressWithLabel variant="determinate" value={availableCount} title={' *'} />
            <ClothesFormStepper initialValues={initialValues} availableSizes={availableSizes} >
              <StepParent label=" 1.     ()  (,   )" validationSchema={parentSchema} />
              <StepChild label=" 2.   " validationSchema={childSchema} />
            </ClothesFormStepper>
            {/* 
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={(values, actions) => {
              handleSubmit(values, actions)
            }}
          >
            {({ values, isSubmitting, handleSubmit }) => (

              <Form onSubmit={handleSubmit}>

                {isSubmitting ? (
                  <>
                    <div>...    .</div> <br />
                    <CircularProgress size="3rem" />
                  </>
                ) : (
                  <>
                    <FormikStep>
                      <TextInput name="last_name" label="" fullWidth />
                      <TextInput name="first_name" label="'" fullWidth />
                      <TextInput name="middle_name" label="-" fullWidth />

                      <Grid container columnSpacing={2} columns={12}>
                        <Grid item xs={12} sm={6}>
                          <MaskedTextField
                            name="tel"
                            label=" "
                            type="tel"
                            format="+38(###)###-##-##"
                            // valueIsNumericString={true}
                            mask="_"
                            formatResult={true}
                            fullWidth
                            disabled={values?.checked}
                          />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                          <MaskedTextField
                            name="inn"
                            label="  ()"
                            format="##########"
                            type="tel"
                            mask="_"
                            valueIsNumericString={true}
                            fullWidth
                            disabled={values?.checked}
                          />
                        </Grid>
                      </Grid>

                      <Grid item xs={12} sm={7}>
                        <MaskedTextField
                          name="vpo_number"
                          label="  "
                          format="####-##########"
                          // valueIsNumericString={true}
                          formatResult={true}
                          mask="_"
                          type="tel"
                          fullWidth
                        />
                      </Grid>
                    </FormikStep>

                    <FormikStep>
                      <TextInput
                        name="child_doc"
                        label="     "
                        helperText="     .    . : 123456"
                        fullWidth
                      />

                      <MaskedTextField
                        name="child_bday"
                        label=" "
                        format="##.##.####"
                        mask="_"
                        type="tel"
                        // valueIsNumericString={true}
                        formatResult={true}
                        fullWidth
                      />

                      <SelectInput
                        name="size"
                        label="  "
                        options={availableSizes.map(size => `${size.label} ( ${size.available}%)`)}
                        defaultValue=""
                        fullWidth
                      />

                      <CheckField
                        name="agree"
                        label="      "
                        sx={{ mb: 2, textAlign: 'left' }}
                      />
                    </FormikStep>


                    <Button
                      startIcon={isSubmitting ? <CircularProgress size="1rem" /> : null}
                      disabled={isSubmitting}
                      variant="contained"
                      color="primary"
                      size="large"
                      type="submit"
                    >
                      {isSubmitting ? '' : ''}
                    </Button>
                  </>
                )}



              </Form>
            )}

          </Formik>

           */}



          </>
        )}
        {datestamp && (
          <Typography color="text.secondary" variant="body2" component="div" sx={{ my: 4 }}>
            * -    {datestamp}
          </Typography>
        )}
      </div>
    </FormContext.Provider>
  )
}

export default ClothesForm