import FormikStep from 'components/FormikStep'
import TextInput from 'components/inputs/TextInput'
import MaskedTextField from 'components/inputs/MaskedTextField'
import { Grid } from '@mui/material'

import * as yup from 'yup'


const phoneNumberRegex =
  /\(?([0-9]{3})\)?([0-9]{3})[-. ]?([0-9]{2})[-. ]?([0-9]{2})$/

const parentSchema = yup.object({
  last_name: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(2, '  '),
  first_name: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(2, "  '"),
  middle_name: yup
    .string()
    .required("  '")
    .matches(/^\D+$/, '     ')
    .min(4, '     4 '),
  tel: yup
    .string()
    .required("  '")
    .matches(
      phoneNumberRegex,
      '  , (0)--'
    ),
  inn: yup
    .string()
    .required("  '")
    .length(10, '    10 '),
  vpo_number: yup
    .string()
    .required("  '")
    .matches(/^\d{4}[-]\d{10}$/, '  (1234-1234567890)')
})

const StepParent = ({ label }) => {

  return (
    <FormikStep>

      <TextInput name="last_name" label="" fullWidth autoComplete="none" />
      <TextInput name="first_name" label="'" fullWidth autoComplete="none" />
      <TextInput name="middle_name" label="-" fullWidth autoComplete="none" />

      <Grid container columnSpacing={2} columns={12}>
        <Grid item xs={12} sm={6}>
          <MaskedTextField
            name="tel"
            label=" "
            type="tel"
            format="+38(###)###-##-##"
            // valueIsNumericString={true}
            mask="_"
            formatResult={true}
            fullWidth
          // disabled={values?.checked}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <MaskedTextField
            name="inn"
            label="  ()"
            format="##########"
            type="tel"
            mask="_"
            valueIsNumericString={true}
            fullWidth
          // disabled={values?.checked}
          />
        </Grid>
      </Grid>

      <Grid item xs={12} sm={7}>
        <MaskedTextField
          name="vpo_number"
          label="  "
          format="####-##########"
          // valueIsNumericString={true}
          formatResult={true}
          mask="_"
          type="tel"
          fullWidth
        />
      </Grid>
    </FormikStep>
  )
}

export {
  StepParent,
  parentSchema
}