import { useContext } from 'react'
import FormikStep from 'components/FormikStep'
import TextInput from 'components/inputs/TextInput'
import MaskedTextField from 'components/inputs/MaskedTextField'
import SelectInput from 'components/inputs/SelectInput'
import CheckField from 'components/inputs/CheckField'
import { FormContext } from 'components/forms/ClothesForm/ClothesForm'

import * as yup from 'yup'
import { parseDateString } from 'helpers/date'

import addDays from 'date-fns/addDays'
import addYears from 'date-fns/addYears'
import format from 'date-fns/format'

const today = new Date()
const minDate = addYears(today, -18)
const oneDayBeforeMinDate = addDays(minDate, -1)

const childSchema = yup.object({
  child_doc: yup
    .string()
    .required("  '")
    .matches(/^[A-Z-]{2,}\d{6}$/, ' '),
  child_bday: yup
    .date()
    .required("  '")
    .transform(parseDateString)
    .typeError(' ,      ..')
    .min(oneDayBeforeMinDate, `  ${format(minDate, 'dd.MM.yyyy')}`)
    .max(today, '    '),
  size: yup
    .string()
    .required("  '"),
  agree: yup
    .boolean()
    .oneOf([true], ' ,   '),
})

const StepChild = () => {
  const { availableSizes } = useContext(FormContext);

  return (
    <FormikStep>
      <TextInput
        name="child_doc"
        label="     "
        helperText="    .    . : 123456"
        fullWidth
      />

      <MaskedTextField
        name="child_bday"
        label=" "
        format="##.##.####"
        mask="_"
        type="tel"
        // valueIsNumericString={true}
        formatResult={true}
        fullWidth
      />

      <SelectInput
        name="size"
        label="  "
        options={availableSizes.map(size => {
          return {
            ...size,
            label: ` ${size.label} ( ${size.available}%)`
          }
        })}
        defaultValue=""
        fullWidth
      />

      <CheckField
        name="agree"
        label="      "
        sx={{ mb: 2, textAlign: 'left' }}
      />
    </FormikStep>
  )
}

export {
  StepChild,
  childSchema
}