import React from 'react'
import { Formik, Form } from 'formik'
import {
  Button,
  Stepper,
  Step,
  StepLabel,
  CircularProgress,
  Typography
} from '@mui/material'
import Swal from 'sweetalert2'

import { useWindowSize } from 'hooks'
import { action } from 'api'
import { getFormData } from 'helpers/normalizeData'

function FormikStepper({ children, ...props }) {
  const childrenArray = React.Children.toArray(children)
  const [step, setStep] = React.useState(0)
  const [completed, setCompleted] = React.useState(false)

  const currentChild = childrenArray[step]
  const isLastStep = () => step === childrenArray.length - 1
  const isFirstStep = () => step === 0
  const { width } = useWindowSize()
  const isMobile = width <= 576

  const handleSubmit = async (values, helpers) => {
    if (isFirstStep()) {
      const params = {
        phoneNumber: values?.phoneNumber,
        inn: values?.inn
      }
      try {
        await fetch(action + '?' + new URLSearchParams(params), {
          method: 'GET'
        })
          .then((response) => response.json())
          .then((data) => {
            console.log(data)
            switch (data?.result) {
              case 'notFoundAll':
                setStep((s) => s + 1)
                helpers.setFieldValue('checked', true)
                helpers.setTouched({})
                break
              case 'success':
                Swal.fire('!', `  .  : ${data?.number}`, 'warning')
                helpers?.resetForm()
                setStep(0)
                break
              case 'different':
                Swal.fire(' ', '     ,        .    .     ,      . ', 'error')
                helpers?.resetForm()
                setStep(0)
                break
              case 'notFoundOne':
                Swal.fire('', '          .', 'error')
                helpers?.resetForm()
                setStep(0)
                break
              case 'invalidInn':
                Swal.fire('!', '      ,       .      ,    ,       !', 'warning')
                helpers?.resetForm()
                setStep(0)
                break
              case 'error':
                Swal.fire({
                  title: '...',
                  text: `  : ${data.error}`,
                  icon: 'error',
                  confirmButtonText: ' '
                })
                helpers?.resetForm()
                setStep(0)
                break
              default:
                Swal.fire(' ', '\'  ', 'error')
                helpers?.resetForm()
                setStep(0)
            }

          })
      } catch (error) {
        throw new Error(error)
      }
    } else if (isLastStep()) {
      const toNormalize = ['firstName', 'lastName', 'middleName', 'street']
      const toExclude = ['agree', 'checked']

      const data = getFormData(
        values,
        toNormalize,
        toExclude
      )

      try {
        await fetch(action, {
          method: 'POST',
          body: data

        })
          .then((response) => response.json())
          .then((data) => {
            // console.log(data)

            if (data.result === 'success') {
              Swal.fire('!', `     .  : <br>
              <strong style="font-size: 1.5em;">${data.number}</strong><br>
                  ().`, 'success')
              setCompleted(false)
              setStep(0)
              helpers.resetForm()

            }
            if (data.result === 'refused') {
              Swal.fire('', data.reason, 'error')
              // <p>   <strong>${data.number}</strong></p>
            }

            if (data.result === 'timeout') {
              Swal.fire('', '  .  .', 'warning')
            }

            if (data.result === 'error') {
              Swal.fire({
                title: '...',
                text: `   .     .  .
                 : ${data.error}. `,
                icon: 'error',
                confirmButtonText: ''
              })
            }
          })
      } catch (error) {
        throw new Error(error)
      }
    } else {
      setStep((s) => s + 1)
      helpers.setTouched({})
      window.scrollTo(0, 0)
    }
  }

  const handleReset = async (resetForm) => {
    Swal.fire({
      title: ' ?',
      text: "    .",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: ',  ',
      cancelButtonText: ''
    }).then((result) => {
      if (result.isConfirmed) {
        resetForm()
        setStep(0)
        Swal.fire({
          icon: 'success',
          title: ' ',
          toast: true,
          position: 'bottom-start',
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true,
        })
      }
    })
  }

  const adaptiveStepper = isMobile ? (<Typography sx={{ textAlign: 'center', mb: 3 }}> {step + 1} / {childrenArray.length}. {currentChild?.props?.label}</Typography>) : (
    <Stepper
      alternativeLabel
      activeStep={step}
      sx={{ mb: 4 }}
    // orientation="vertical"
    >
      {childrenArray.map((child, index) => (
        <Step
          key={child.props.label}
          completed={step > index || completed}
        >
          <StepLabel>{child.props.label}</StepLabel>
        </Step>
      ))}
    </Stepper>
  )

  return (
    <Formik
      {...props}
      validationSchema={currentChild.props.validationSchema}
      onSubmit={handleSubmit}
    >
      {({ isSubmitting, values, resetForm }) => (

        <Form autoComplete="off">

          {width && adaptiveStepper}

          {currentChild}

          <div className="form-nav__container">
            <Button
              disabled={isSubmitting}
              color="error"
              variant="outlined"
              size="large"
              onClick={() => handleReset(resetForm)}
              sx={{ mr: 1.5, mb: 2 }}
            >
              
            </Button>

            {step > 0 ? (
              <Button
                disabled={isSubmitting}
                color="primary"
                variant="outlined"
                size="large"
                onClick={() => {
                  setStep((s) => s - 1)
                  window.scrollTo(0, 0)
                }}
                sx={{ mr: 1.5, mb: 2 }}
              >
                
              </Button>
            ) : null}

            <Button
              startIcon={isSubmitting ? <CircularProgress size="1rem" /> : null}
              disabled={isSubmitting}
              variant="contained"
              color="primary"
              size="large"
              type="submit"
              sx={{ mb: 2 }}
            >
              {isSubmitting ? '' : isLastStep() ? '' : ''}
            </Button>
          </div>

        </Form>
      )}
    </Formik>
  )
}

export default FormikStepper