import FormikStep from 'components/FormikStep'
import SelectInput from 'components/inputs/SelectInput'
import TextInput from 'components/inputs/TextInput'
import StreetField from 'components/inputs/StreetField'
import { Grid } from '@mui/material'
import { streets, cities } from 'helpers/toponyms'

import * as yup from 'yup'

const addressSchema = yup.object({
  city: yup.string().required("  '"),
  street: yup.string().required("  '"),
  addrNum: yup.number().required("  '").min(1, '  - 1'),
  addrRoom: yup.number().min(1, '  - 1'),
})

const StepAddress = () => {
  return (
    <FormikStep>
      <SelectInput
        name="city"
        label=" "
        options={cities}
        fullWidth
      />

      <StreetField name="street" label="" streets={streets} />

      <Grid container columnSpacing={2} columns={11}>
        <Grid item xs={12} sm={4}>
          <TextInput name="addrNum" label=" " type="number" />
        </Grid>
        <Grid item xs={12} sm={3}>
          <TextInput name="addrCorp" label="" />
        </Grid>
        <Grid item xs={12} sm={4}>
          <TextInput name="addrRoom" label="" type="number" />
        </Grid>
      </Grid>
    </FormikStep>
  )
}

export {
  addressSchema,
  StepAddress
}