import { Formik, Form } from 'formik'
import {
  Button,
  CircularProgress,
  Typography
} from '@mui/material'
import { StepCheck, checkSchema } from 'components/steps/StepCheck'
import Section from './layout/Section'
import Swal from 'sweetalert2'

import { action } from 'api'


const initialValues = {
  inn: '',
  phoneNumber: '',
}

const ReminderForm = () => {

  const handleSubmit = async (values, helpers) => {
    const params = {
      phoneNumber: values?.phoneNumber,
      inn: values?.inn
    }
    try {
      await fetch(action + '?' + new URLSearchParams(params), {
        method: 'GET'
      })
        .then((response) => response.json())
        .then((data) => {
          switch (data?.result) {
            case 'notFoundAll':
            case 'notFoundOne':
              Swal.fire(' ', '      !', 'error')
              break
            case 'success':
              Swal.fire({
                title: `${data.number}`,
                text: `   `,
                icon: 'info',
                confirmButtonText: ''
              })
              break
            case 'different':
              Swal.fire(' ', '     ,        .    .     ,      . ', 'warning')
              break
            case 'invalidInn':
              Swal.fire('!', '      ,       .      ,    ,       !', 'warning')
              break
            case 'error':
              Swal.fire({
                title: '...',
                text: `  : ${data.error}`,
                icon: 'error',
                confirmButtonText: ' '
              })
              break
            default:
              Swal.fire(' ', '\'  ', 'error')
          }
        }).finally(() => {
          helpers?.resetForm()
        })
    } catch (error) {
      throw new Error(error)
    }
  }

  const handleReset = async (resetForm) => {
    Swal.fire({
      title: ' ?',
      text: "    .",
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: ',  ',
      cancelButtonText: ''
    }).then((result) => {
      if (result.isConfirmed) {
        resetForm()
        Swal.fire({
          icon: 'success',
          title: ' ',
          toast: true,
          position: 'bottom-start',
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true,
        })
      }
    })
  }

  return (
    <Section maxWidth={'sm'} title={'  '}>

      <Typography color="inherit" variant="caption" component="div" sx={{ textAlign: 'center', mb: 3 }}>
                ,   .
      </Typography>

      <Formik
        initialValues={initialValues}
        validationSchema={checkSchema}
        onSubmit={handleSubmit}
      >
        {({ isSubmitting, resetForm }) => (

          <Form autoComplete="off">

            <StepCheck />

            <div className="form-nav__container">

              {isSubmitting ? <CircularProgress size="3rem" sx={{ margin: "1em auto" }} /> : (
                <>
                  <Button
                    disabled={isSubmitting}
                    color="error"
                    variant="outlined"
                    size="large"
                    onClick={() => handleReset(resetForm)}
                    sx={{ mr: 1.5, mb: 2 }}
                  >
                    
                  </Button>

                  <Button
                    disabled={isSubmitting}
                    variant="contained"
                    color="primary"
                    size="large"
                    type="submit"
                    sx={{ mb: 2 }}
                  >
                    
                  </Button>
                </>
              )}


            </div>

          </Form>
        )}
      </Formik>

    </Section>
  )
}

export default ReminderForm